<template>
    <div class="hd">
        <div class="hd__body">
            <img src="../../../assets/images/gong.png" alt="" class="hd__logo">
            <div class="hd__title">中国共青团</div>
            <div v-if="!isTos" class="hd__username">您好！{{ username }}</div>
            <svg v-if="!isTos" class="hd__logout-icon" t="1598059419662" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="1973" width="200" height="200"><path d="M267.306667 162.432l48.938666 69.930667a341.333333 341.333333 0 1 0 391.509334 0l48.938666-69.930667A426.154667 426.154667 0 0 1 938.666667 512c0 235.648-191.018667 426.666667-426.666667 426.666667S85.333333 747.648 85.333333 512a426.154667 426.154667 0 0 1 181.973334-349.568zM469.333333 512V85.333333h85.333334v426.666667h-85.333334z" p-id="1974"></path></svg>
            <div v-if="!isTos" class="hd__logout" @click="logout">退出</div>
        </div>
    </div>
</template>


<script>
import { userLogout } from '@/api/user.js';
import { adminLogout } from '@/api/admin.js';
export default {
    props: {
        username: {
            type: String,
            default: 'alice'
        },
        isAdmin: {
            type: Boolean,
            default: false
        },
        isTos: {
            type: Boolean,
            default: false
        }
    },
    methods: {
        logout() {
            if (this.isAdmin) {
                adminLogout()
                .then(res => {
                    console.log('res', res)
                    window.location = '/TwLogin'
                })
                .catch(err => {
                    console.log('err', err);
                })
            } else {
                userLogout()
                .then(res => {
                    console.log('res', res)
                    window.location = '/UserLogin'
                })
                .catch(err => {
                    console.log('err', err);
                })
            }
        }
    }
}
</script>

<style lang="less">
.hd {
    height: 64px;
    background-color: #FF633F;
    color: #ffffff;
    display: flex;
    flex-direction: row;
    align-items: center;
    font-size: 14px;
    line-height: 1;
    &__body {
        width: 1200px;
        display: flex;
        align-items: center;
        flex-direction: row;
        margin: 0 auto;
    }
    &__username {
        margin-left: auto;
    }
    &__logo {
        width: 52px;
        height: 52px;
    }
    &__title {
        font-weight: bold;
        font-size: 25px;
        margin-left: 6px;
        letter-spacing: 1px;
    }
    &__logout {
        &:hover {
            cursor: pointer;
            color: darken(#fff, 10%);
        }
        &:active {
            color: darken(#fff, 20%);
        }
    }
    &__logout-icon {
        margin-left: 12px;
        padding-left: 12px;
        border-left: 2px solid #eee;
        height: 16px;
        width: 28px;
        fill: #fff;
    }
    &__logout {
        margin-left: 6px;
    }
}
</style>